.card__client {
    background: #FFFFFF;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.06), 0px 10px 20px -8px rgba(0, 0, 0, 0.16);
    border-radius: 8px;
    max-width: 180px;
    padding: 10px;
    
    display: none;
}
.card__client-name {
    display: flex;
    align-items: center;
    img {
        width: 40px;
        height: 40px;
        margin-right: 10px;
    }
}
.name__client {
    font-weight: 600;
    font-size: 12px;
    line-height: 120%;
    letter-spacing: -0.05em;
    color: #795BDA;
}
.card__client-info {

}
.info__list {
    list-style: none;
    padding-left: 0;
    li {
        display: flex;
        justify-content: space-between;
        font-weight: 500;
        font-size: 12px;
        line-height: 120%;
        letter-spacing: -0.05em;
        color: #222B40;
        margin-bottom: 10px;
        text-align: left;
    }
}
.card__client-btns {
    .btns__list {
        list-style: none;
        padding-left: 0;
        li {
            border-top: 1px solid rgba(34, 43, 64, 0.1);
            button {
                font-weight: 600;
                font-size: 14px;
                line-height: 100%;
                letter-spacing: -0.06em;
                color: #795BDA;
                padding: 10px 0;
                border: none;
                background: transparent;
                display: block;
                margin: 0 auto;
                cursor: pointer;
            }
            .cancel_btn, .abolition {
                color: #FF5C8D;
            }
        }
    }
    .abolition_list {
        display: none;
    }
}

@media screen and (max-width:744px) {
    .schedule-block_wrap {
        max-width: 100%;
    }
    .work__schedule_exemple {
        display: none;
    }
    .work__schedule_mobile {
        display: block;
    }
    .work__schedule-date {
        width: 130px;
    }
    .work__schedule-arrows {
        margin-left: 7px;
    }
    .work__schedule-controls {
        padding: 20px;
    }
}